define('todomvc-ember-cli/tests/helpers/delay', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  function delay(ms) {
    return function () {
      return new Ember['default'].RSVP.Promise(function (resolve, reject) {
        setTimeout(function () {
          resolve();
        }, ms);
      });
    };
  }
  exports['default'] = delay;

});